<template>

    <div class="panel-body">
        <!-- Main content -->
        <div class="content-wrapper  col-sm-12">
            <div class="panel panel-flat">
                <div class="table-responsive">
                    <table class="table table-bordered">
                        <thead>
                            <tr>
                                <th>Supplier</th>
                                <th colspan="2">Action</th>
                            </tr>
                        </thead>
                        <tbody>
                            <tr>
                                <td>
                                    <input type="text" class="form-control" placeholder="RS #" v-model="p_rsno"
                                        v-on:keyup.enter="fetch_list_request()"> <br>
                                    <p v-show="loading_supplier" class="pull-center">
                                        <i class="icon-spinner4 spinner position-left"></i> PLEASE WAIT ..
                                    </p>
                                    <select v-show="!loading_supplier" class="form-control" v-model="p_supplier"
                                        style="text-align:center !important;">
                                        <option value=""> -- SELECT SUPPLIER -- </option>
                                        <option v-bind:value="row.vendorcode" v-for="(row, i) in list_supplier"
                                            :key="i"> {{ row.description }}</option>
                                    </select>
                                    <label for="dateFrom" style="margin-top:10px;">Date From</label>
                                    <input type="date" v-model="dateFrom" class="form-control" name="dateFrom">
                                    <label for="dateTo" style="margin-top:10px;">Date To</label>
                                    <input type="date" v-model="dateTo" class="form-control" name="dateTo">
                                </td>
                                <td colspan="2">
                                    <button class="btn btn-info btn-xs" v-on:click="fetch_list_request()"><i
                                            class="icon-search4"></i></button>
                                </td>
                            </tr>
                            <tr v-show="load_blkbtn">
                                <td></td>
                                <td colspan="2"> <button type="button" class="btn btn-primary btn-sm"
                                        @click="btnblkpickUp">BULK PICKUP</button> </td>
                            </tr>
                            <tr v-for="(row, i) in list_request" :key="i">
                                <td>
                                    <b>RS #: <a href="#" v-on:click="boDetails(1, row.rs_id)">{{ row.rs_id }}</a><br>
                                        DATE: {{ row.rs_date | date }}<br>
                                        SUPPLIER: {{ row.supplier_code }} <br>
                                        <span v-if="row.rs_action == '1' && row.processed_by != '0'">
                                            STATUS: RETURNED |<a href="#" v-on:click="boDetails(2, row.rs_id)"> R2SSA #
                                                {{ row.movement_no }} </a>| {{ row.rs_date | date }} <br>
                                        </span>
                                        <span v-if="row.rs_action == '2' && row.processed_by != '0'">
                                            STATUS: DISPOSED |<a href="#" v-on:click="boDetails(2, row.rs_id)"> FDFB # {{
                                                row.movement_no }} </a>| {{ row.rs_date | date }} <br>
                                        </span>
                                        <span v-if="row.rs_action != '0' && row.processed_by != '0'">
                                            <a href="#" v-on:click="print_rs(row.rs_id, 0)"> Supplier Copy |</a>
                                            <a href="#" v-on:click="print_rs(row.rs_id, 1)"> Warehouse Copy |</a>
                                            <a href="#" v-on:click="print_rs(row.rs_id, 2)"> Accounting Copy </a> <br>
                                        </span>
                                        CREATED BY: {{ row.created_by }} <br>
                                        PROCESSED BY: {{ row.processed_by }} <br>
                                        <span v-if="row.rs_action != '0'">
                                            PICKUP STATUS: {{ (row.picked_up === null) ? 'PENDING' : 'COMPLETED' }} <br>
                                        </span> </b>
                                </td>
                                <td>
                                    <button type="button" v-if="row.picked_up === null"
                                        @click="btnpickUp(row.rs_id, row.rs_action)" class="btn btn-primary btn-sm"><i
                                            class="icon-user"></i>Pickup</button>
                                    <button type="button" v-else @click="btnView(row.rs_id, row.rs_action)"
                                        class="btn btn-primary btn-sm"><i class="icon-user"></i>View</button>

                                    <!-- <router-link :to="{ name: 'RsPickUp', params: { rs_id: row.rs_id, rs_action: row.rs_action }}">
                                    <button type="button" class="btn btn-primary btn-sm" ><i class="icon-user"></i> {{ (row.picked_up === null) ? 'Pickup' : 'View' }} </button>
                                </router-link> -->
                                </td>
                                <td>
                                    <input type="checkbox" :disabled="row.picked_up != null" v-model="bulk_rsid"
                                        :value="row.rs_id" multiple>
                                </td>
                                <!-- <td hidden><input type="hidden" v-bind:name="'supplier_code'+ i" v-bind:value="row.supplier_code"></td> -->
                            </tr>
                        </tbody>
                    </table>
                </div>
            </div>
        </div>


    </div>


</template>
<script>

</script>
<script>
import axios from 'axios'
import moment from 'moment'
import { msgError, msgSuccess, tokenApi, apiUrl } from './../../assets/custom/custom.js'
import { async } from 'q'

export default {
    data (){
        return {
           p_rsno: '',
           p_supplier: '',
           dateFrom: '',
           dateTo: '',
           list_supplier: [],
           list_request: [],
           loading_supplier: false,
           bulk_rsid: [],
           load_blkbtn: false
        }
    },
    watch: {
        bulk_rsid: {
            handler: function() {
                (this.bulk_rsid.length > 1) ? this.load_blkbtn = true : this.load_blkbtn = false
            }, deep: true
        }
    },
    computed: {
         message(){
             return this.$store.getters.message
         }
    },
    async mounted(){
        await this.fetch_list_supplier(),
        await this.fetch_list_request()
    },
    methods: {
        async btnblkpickUp() {
            this.$router.push({ name: 'RsPickUpBulk', params: { rs_info: JSON.stringify(this.bulk_rsid), rs_action: '1' }})
        },
        async btnpickUp(rs_id, rs_action) {
            this.$router.push({ name: 'RsPickUp', params: { rs_id: rs_id, rs_action: rs_action }})
        },
        async btnView(rs_id, rs_action) {
            this.$router.push({ name: 'RsPickUpView', params: { rs_id: rs_id, rs_action: rs_action }})
        },
        async fetch_list_supplier() {
            this.loading_supplier = true //activate loading
            try {
                let query = {
                    headers: tokenApi('token', 'header')
                }
                let result = await axios.get(apiUrl+'purchaser/inquiry_rs/fetch_list_supplier', query)
                let data   = result.data
                this.list_supplier = data.supplier

            } catch (error) {
                await this.handdleError(error)
            }
            this.loading_supplier = false //deactivate loading
        },
        async fetch_list_request() {
            try {
                let datas = {
                    params: {
                        dateFrom: this.dateFrom,
                        dateTo: this.dateTo,
                        supplierCode: this.p_supplier,
                        status: '0',
                        rsId: this.p_rsno,
                        rs_action: 1,
                        branchCode: 'srsn',
                        user_id: this.$session.get('user_id')
                    },
                    headers: tokenApi('token', 'header')
                }
                let result = await axios.get(apiUrl+'purchaser/inquiry_rs/getListRequest', datas)
                let data = result.data
                this.list_request = data.listRequest
            } catch (error) {
                await this.handdleError(error)
            }
        },
        async handdleError(error=null) {
            if (error != null) {
                const response = JSON.parse(JSON.stringify(error))
                const status = response.response.status.toString()
                const message = (response.response.data.error === undefined) ? response.response.data.message : response.response.data.error.message
                msgError(status, message)
                return true
            }
                msgError(401, "Something Wrong")
        },
        async print_rs(rsid, type) {
            let data = { 
                params: {
                    rs_id: rsid,
                    type: type,
                    user_id: this.$session.get('user_id'),
                    user_fullname: this.$session.get('user_fullname')
                },
                headers: tokenApi('token', 'header') 
            }
            let result = await axios.get(apiUrl+'purchaser/inquiry_rs/print_rs_dc', data)
            .then(async (response) => {
                this.dataforprint = (response.data.forprint)
                         
                var header7 = ''
                var header8 = ''
                var header9 = '' 
                var header10 = '' 
                var header11 = '' 
                var header12 = ''
                var header13 = ''
                var header14 = '' 
                var header15 = ''
                var header16 = ''
                var header17 = ''
                let header1 = `RETURNS and DISPOSAL ` + this.dataforprint["branchname"] + '\n'
                let header2 = this.dataforprint["headers"]
                let header3 = `Supplier: ${this.dataforprint["supplier"]} \n`
                let header4 = `Date: ${this.dataforprint["date"]} \n`
                let header5 = `U.COST \n`
                let header6 = `Product                                                                       QTY\n`
                let coyid = this.dataforprint["coy"]
                let totalQty
                let qty = 0
                let totalCost = []
                let cost = 0
                let coyname = ['*** Supplier\'s Copy ***','*** Warehouse Copy ***','*** Accounting Copy ***']
                let line = ""
                let line2 = ""
                let nl = ` \n  \n  \n  \n  \n`
                for(const rows of this.dataforprint.details){
                    let item = rows.item_name
                    let spacing = header6.length - item.length

                    qty += rows.qty
                    totalQty = qty
                    cost += parseFloat(rows.qty) * parseFloat(rows.price)

                    totalCost.push(cost)

                    if (item.length >= 40) {
                        item = item.replace(/ /g,'')
                        item = item.slice(0,40)
                        spacing = header6.length - item.length
                    }

                    for (let i = 0; i < spacing - 30; i++) {
                        item += " "
                    }
                    let cost2 = (rows.qty*rows.price).toFixed(2)
                    item += rows.qty + `\n` + cost2 + `\n` + rows.uom + `\n` 
                    header7 += item
                    
                    
                }
                console.log(header7)

                for (let x = 0; x < header6.length - 10; x++) {
                    line += `-`
                    
                }
                line2 += `_______________________________________________`
                header8 = line +`\n`
                header9 = `                                   TOTAL QTY: ` + this.dataforprint["totalQty"] + `\n`
                header10 = `                         TOTAL AMOUNT: ` + this.dataforprint["totalAmt"] +`\n`+line+`\n`
                header11 = `Prepared by:`+ this.dataforprint["currentName"] +`\n`
                header12 = `Checked by:`+ this.dataforprint["currentName"] +`\n`
                header13 = `Received by: \n`
                header14 = line2 + `\n`
                header15 = `                               *** Supplier\'s Copy *** ` +nl
                header16 = `                               *** Warehouse Copy ***  ` + nl + ` `+ header8 +` `+nl
                header17 = `                               ${coyname[coyid]} ` +nl + `` +nl
                
                if(coyid == 3)
                {
                    window.qsprint.printdevice(header1,header2,header3,header4,header5,header6,header7,header8,header9,header10,header11,header12,header13,header14,header15,nl,header16)
                }
                else{
                    window.qsprint.reprintdevice(header1,header2,header3,header4,header5,header6,header7,header8,header9,header10,header11,header12,header13,header14,header15,nl,header16,header17)
                }
                msgSuccess(200, "Successfully printed!", "Success")
                await this.fetch_list_request()
                await this.fetch_list_supplier()
            })
            .catch(async (response) => {
                msgError(401, response)
            })
        },
        async boDetails(type, rs_id) {
            const view = (type == 1) ? 'view_rs' : 'movement_rs'
            this.$router.push({ name: 'RsDetails', params: { rs_id: rs_id, type: view }})
        }
    }
}

</script>


<style scoped>
input {
    border-radius: 0px !important;
    font-size: 10px !important;
    text-align: center;
}

select {
    border-radius: 0px !important;
    text-align-last: center;
}

.btn {
    border-radius: 0px;
}

th,
td {
    padding: 10px !important;
    font-size: 10px;
    text-align: center !important;
}
</style>
